header {
    background: purple;
    width: 100%;
    min-height: 80px;
    color: ivory;
    position: fixed;
    top: 0;
    margin: 0 auto;
    box-shadow: 0 5px 5px darkslategrey;
    z-index: 1000;
    font-size: 20px;
}

header ul {
    list-style-type: none;
    margin: 0 auto;
    padding: 0;
    width: 100%;
    height: 100%;
    text-align: center;
}

header li {
    display: inline-block;
    width: 33%;
    padding-top: 20px;
}
.card {
    width: 150px;
    height: 150px;
    margin-right: 20px;
    margin-bottom: 20px;
    background-size: cover;
    box-shadow: 5px 5px 5px darkslategrey;
    border-radius: 5px;
}

.card:hover {
    border: 4px solid darkslategrey;
}

footer {
    background: purple;
    width: 100%;
    min-height: 35px;
    color: ivory;
    position: absolute;
    bottom: 0;
    margin: 0 auto;
    box-shadow: 0 0 5px 5px darkslategrey ;
    font-size: 16px;
    margin-top: 50px;
}

img {
    height: 30px;
    width: 30px;

}

.container {
    display: flex;
    min-height: 80%;
    flex-wrap: wrap;
    justify-content: center;
    padding-top: 50px;
    padding-bottom: 50px;
    width: 90%;
    margin: 0 auto;
    z-index: -1;
}
